import { useStaticQuery, graphql } from "gatsby"

export const usePortfolioPages = () => {
  const { allMdx } = useStaticQuery(
    graphql`
      query PortfolioPages {
        allMdx(
          filter: { slug: { regex: "/portfolio//" } }
          sort: { fields: slug, order: DESC }
        ) {
          edges {
            node {
              id
              slug
              excerpt
              body
              frontmatter {
                title
                caption
                categories
                slug
                tags
                cover {
                  childImageSharp {
                    gatsbyImageData(
                      quality: 100
                      placeholder: TRACED_SVG
                      formats: [AUTO, WEBP, AVIF]
                    )
                  }
                }
                extra {
                  workExternalLink {
                    name
                    href
                  }
                }
              }
            }
          }
        }
      }
    `
  )
  return allMdx.edges
}
